import React, { useState, useEffect, useContext } from "react";
import {
  Box,
  Typography,
  Grid,
  Button,
  CircularProgress,
  Fade,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  IconButton,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from "@mui/material";
import { Delete, ExpandMore } from "@mui/icons-material";
import axiosInstance from "../../axiosConfig";
import { UserContext } from "../../UserContext";
import MovieCard from "../../components/MovieCard";

export default function ManageMedia() {
  const { userId, role } = useContext(UserContext); // Assume role indicates "Admin"
  const [mediaItems, setMediaItems] = useState([]);
  const [seriesDetails, setSeriesDetails] = useState({});
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [deleteDialog, setDeleteDialog] = useState({
    open: false,
    type: "", // "media", "season", "episode"
    id: null,
    title: "",
  });

  useEffect(() => {
    if (role !== "Admin") {
      setError("Unauthorized access. Admin role required.");
      return;
    }

    const fetchMedia = async () => {
      setLoading(true);
      setError("");

      try {
        const response = await axiosInstance.get("/Admin/GetMedia");
        setMediaItems(response.data);

        // Fetch details for series
        const series = response.data.filter(
          (item) => item.type?.toLowerCase() === "series"
        );
        const detailsPromises = series.map((item) =>
          axiosInstance.get("/User/GetMediaDetails", {
            params: { mediaId: item.mediaId },
          })
        );
        const detailsResponses = await Promise.all(detailsPromises);
        const detailsMap = {};
        detailsResponses.forEach((res, index) => {
          detailsMap[series[index].mediaId] = res.data;
        });
        setSeriesDetails(detailsMap);
      } catch (err) {
        setError(err.response?.data?.error || "Failed to fetch media");
      } finally {
        setLoading(false);
      }
    };

    fetchMedia();
  }, [role]);

  const handleDelete = async () => {
    try {
      const { type, id } = deleteDialog;
      if (type === "media") {
        await axiosInstance.delete(`/Admin/DeleteMedia/${id}`);
      } else if (type === "season") {
        await axiosInstance.delete(`/Admin/DeleteSeason/${id}`);
      } else if (type === "episode") {
        await axiosInstance.delete(`/Admin/DeleteEpisode/${id}`);
      }
      // Refresh media list
      const response = await axiosInstance.get("/User/GetMedia");
      setMediaItems(response.data);
      const series = response.data.filter(
        (item) => item.type?.toLowerCase() === "series"
      );
      const detailsPromises = series.map((item) =>
        axiosInstance.get("/User/GetMediaDetails", {
          params: { mediaId: item.mediaId },
        })
      );
      const detailsResponses = await Promise.all(detailsPromises);
      const detailsMap = {};
      detailsResponses.forEach((res, index) => {
        detailsMap[series[index].mediaId] = res.data;
      });
      setSeriesDetails(detailsMap);
      setDeleteDialog({ open: false, type: "", id: null, title: "" });
    } catch (err) {
      setError(err.response?.data?.error || "Failed to delete item");
      setDeleteDialog({ open: false, type: "", id: null, title: "" });
    }
  };

  const openDeleteDialog = (type, id, title) => {
    setDeleteDialog({ open: true, type, id, title });
  };

  const closeDeleteDialog = () => {
    setDeleteDialog({ open: false, type: "", id: null, title: "" });
  };

  if (loading) {
    return (
      <Fade in={loading} timeout={400}>
        <Box
          sx={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            justifyContent: "center",
            minHeight: "100vh",
            bgcolor: "background.default",
          }}
        >
          <CircularProgress sx={{ color: "primary.main", mb: 2 }} />
          <Typography
            variant="body1"
            sx={{ color: "text.secondary", fontWeight: 500 }}
          >
            Loading media...
          </Typography>
        </Box>
      </Fade>
    );
  }

  if (error) {
    return (
      <Fade in={!!error} timeout={400}>
        <Box
          sx={{
            bgcolor: "rgba(255, 75, 75, 0.1)",
            border: "1px solid rgba(255, 75, 75, 0.3)",
            borderRadius: 2,
            p: 2,
            m: 3,
            textAlign: "center",
            maxWidth: "600px",
            mx: "auto",
          }}
        >
          <Typography
            variant="body1"
            sx={{ color: "error.main", fontWeight: 500 }}
          >
            {error}
          </Typography>
        </Box>
      </Fade>
    );
  }

  const movies = mediaItems.filter(
    (item) => item.type?.toLowerCase() === "movie"
  );
  const series = mediaItems.filter(
    (item) => item.type?.toLowerCase() === "series"
  );

  return (
    <Box
      sx={{
        minHeight: "100vh",
        bgcolor: "background.default",
        py: { xs: 3, sm: 4 },
        px: { xs: 2, sm: 3, md: 4 },
      }}
    >
      <Fade in timeout={600}>
        <Box>
          <Typography
            variant="h3"
            sx={{
              fontWeight: 700,
              color: "text.primary",
              mb: 4,
              letterSpacing: "-0.025em",
            }}
          >
            Admin Media Management
          </Typography>

          {/* Movies Section */}
          <Box sx={{ mb: 6 }}>
            <Typography
              variant="h4"
              sx={{
                fontWeight: 700,
                color: "text.primary",
                mb: 3,
                letterSpacing: "-0.025em",
              }}
            >
              Movies
            </Typography>
            <Grid container spacing={{ xs: 2, sm: 3 }}>
              {movies.length > 0 ? (
                movies.map((item) => (
                  <Grid item xs={12} sm={6} md={4} lg={3} key={item.mediaId}>
                    <Box sx={{ position: "relative" }}>
                      <MovieCard
                        title={item.title || "No title"}
                        description={item.description || "-"}
                        posterUrl={
                          item.thumbnailPath ? `/${item.thumbnailPath}` : ""
                        }
                        trailerUrl={
                          item.trailerPath ? `/${item.trailerPath}` : ""
                        }
                        mediaId={item.mediaId}
                      />
                      <IconButton
                        sx={{
                          position: "absolute",
                          top: 8,
                          right: 8,
                          bgcolor: "rgba(0,0,0,0.6)",
                          color: "error.main",
                          "&:hover": { bgcolor: "rgba(0,0,0,0.8)" },
                        }}
                        onClick={() =>
                          openDeleteDialog("media", item.mediaId, item.title)
                        }
                        aria-label={`Delete ${item.title}`}
                      >
                        <Delete />
                      </IconButton>
                    </Box>
                  </Grid>
                ))
              ) : (
                <Typography
                  variant="body1"
                  sx={{ color: "text.secondary", ml: 2 }}
                >
                  No movies found.
                </Typography>
              )}
            </Grid>
          </Box>

          {/* Series Section */}
          <Box>
            <Typography
              variant="h4"
              sx={{
                fontWeight: 700,
                color: "text.primary",
                mb: 3,
                letterSpacing: "-0.025em",
              }}
            >
              Series
            </Typography>
            {series.length > 0 ? (
              series.map((item) => (
                <Box key={item.mediaId} sx={{ mb: 4 }}>
                  <Box sx={{ position: "relative", mb: 2 }}>
                    <Grid container spacing={{ xs: 2, sm: 3 }}>
                      <Grid item xs={12} sm={6} md={4} lg={3}>
                        <MovieCard
                          title={item.title || "No title"}
                          description={item.description || "-"}
                          posterUrl={
                            item.thumbnailPath ? `/${item.thumbnailPath}` : ""
                          }
                          trailerUrl={
                            item.trailerPath ? `/${item.trailerPath}` : ""
                          }
                          mediaId={item.mediaId}
                        />
                        <IconButton
                          sx={{
                            position: "absolute",
                            top: 8,
                            right: 8,
                            bgcolor: "rgba(0,0,0,0.6)",
                            color: "error.main",
                            "&:hover": { bgcolor: "rgba(0,0,0,0.8)" },
                          }}
                          onClick={() =>
                            openDeleteDialog("media", item.mediaId, item.title)
                          }
                          aria-label={`Delete ${item.title}`}
                        >
                          <Delete />
                        </IconButton>
                      </Grid>
                    </Grid>
                  </Box>
                  {seriesDetails[item.mediaId]?.seasons?.length > 0 && (
                    <Accordion
                      sx={{
                        bgcolor: "background.paper",
                        borderRadius: 2,
                        border: "1px solid rgba(255, 255, 255, 0.1)",
                      }}
                    >
                      <AccordionSummary
                        expandIcon={
                          <ExpandMore sx={{ color: "text.primary" }} />
                        }
                        aria-controls={`panel-${item.mediaId}-content`}
                        id={`panel-${item.mediaId}-header`}
                      >
                        <Typography
                          variant="h6"
                          sx={{ color: "text.primary", fontWeight: 600 }}
                        >
                          Seasons
                        </Typography>
                      </AccordionSummary>
                      <AccordionDetails>
                        {seriesDetails[item.mediaId].seasons.map((season) => (
                          <Box key={season.seasonId} sx={{ mb: 3 }}>
                            <Box
                              sx={{
                                display: "flex",
                                alignItems: "center",
                                mb: 2,
                              }}
                            >
                              <Typography
                                variant="h6"
                                sx={{ color: "text.primary", flexGrow: 1 }}
                              >
                                {season.title ||
                                  `Season ${season.seasonNumber}`}
                              </Typography>
                              <IconButton
                                sx={{
                                  color: "error.main",
                                  "&:hover": {
                                    bgcolor: "rgba(255, 75, 75, 0.1)",
                                  },
                                }}
                                onClick={() =>
                                  openDeleteDialog(
                                    "season",
                                    season.seasonId,
                                    season.title ||
                                      `Season ${season.seasonNumber}`
                                  )
                                }
                                aria-label={`Delete ${
                                  season.title ||
                                  `Season ${season.seasonNumber}`
                                }`}
                              >
                                <Delete />
                              </IconButton>
                            </Box>
                            <Grid container spacing={1.5}>
                              {season.episodes.map((episode) => (
                                <Grid
                                  item
                                  xs={12}
                                  sm={6}
                                  md={4}
                                  lg={3}
                                  key={episode.episodeId}
                                >
                                  <Box
                                    sx={{
                                      display: "flex",
                                      alignItems: "center",
                                      bgcolor: "rgba(255, 255, 255, 0.05)",
                                      borderRadius: 2,
                                      p: 1,
                                    }}
                                  >
                                    <Typography
                                      variant="body2"
                                      sx={{
                                        color: "text.primary",
                                        flexGrow: 1,
                                        overflow: "hidden",
                                        textOverflow: "ellipsis",
                                      }}
                                    >
                                      {episode.title ||
                                        `Episode ${episode.episodeNumber}`}
                                    </Typography>
                                    <IconButton
                                      sx={{
                                        color: "error.main",
                                        "&:hover": {
                                          bgcolor: "rgba(255, 75, 75, 0.1)",
                                        },
                                      }}
                                      onClick={() =>
                                        openDeleteDialog(
                                          "episode",
                                          episode.episodeId,
                                          episode.title ||
                                            `Episode ${episode.episodeNumber}`
                                        )
                                      }
                                      aria-label={`Delete ${
                                        episode.title ||
                                        `Episode ${episode.episodeNumber}`
                                      }`}
                                    >
                                      <Delete />
                                    </IconButton>
                                  </Box>
                                </Grid>
                              ))}
                            </Grid>
                          </Box>
                        ))}
                      </AccordionDetails>
                    </Accordion>
                  )}
                </Box>
              ))
            ) : (
              <Typography
                variant="body1"
                sx={{ color: "text.secondary", ml: 2 }}
              >
                No series found.
              </Typography>
            )}
          </Box>

          {/* Delete Confirmation Dialog */}
          <Dialog
            open={deleteDialog.open}
            onClose={closeDeleteDialog}
            aria-labelledby="delete-dialog-title"
            aria-describedby="delete-dialog-description"
          >
            <DialogTitle id="delete-dialog-title">Confirm Deletion</DialogTitle>
            <DialogContent>
              <DialogContentText id="delete-dialog-description">
                Are you sure you want to delete "{deleteDialog.title}"? This
                action cannot be undone.
                {deleteDialog.type === "media" &&
                  " This will also delete all associated seasons and episodes."}
                {deleteDialog.type === "season" &&
                  " This will also delete all episodes in this season."}
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={closeDeleteDialog} color="primary">
                Cancel
              </Button>
              <Button
                onClick={handleDelete}
                color="error"
                variant="contained"
                autoFocus
              >
                Delete
              </Button>
            </DialogActions>
          </Dialog>
        </Box>
      </Fade>
    </Box>
  );
}
